@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
  --amarelo: #D38312;
  --rosa: #A83279;
  --vermelho: #B95350;
  --laranja: #C76D2E;
  --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%)
}
/* HEADER SYTLE */

.header {
  width: 100%;
  height: 30%;
}

.clouds {
  position: absolute;
  background-image: url("/image/clouds-banner.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: 2;
}


/* NAVBAR STYLES */


.navbar-menu {
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  justify-content: flex-end;
  margin-right: 30px;
  padding-top: 10px;
  position: absolute;
  top: 0;
  z-index: 9;
  width: 100%;
}

.link {
  color: #fff;
  text-decoration: none;
  margin: 0 20px;
  font-size: 2rem;
}

.link:hover {
  color: #fff;
  text-decoration: none;
  border-bottom: 2px solid #fff;
}

.borda {
  border: solid 2px;
  border-image-slice: 1;
  border-image-source: var(--gradiente);
  padding: 3px 5px;
}

.borda:hover {
  font-size: 2rem;
  padding: 7px 10px;
  background-color: rgba(0, 0, 0, 0.2);
}

.logout-btn {
  position: relative;
  z-index: 99;
  border: solid 2px;
  border-image-slice: 1;
  border-image-source: var(--gradiente);
  padding: 3px 5px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  font-size: 2rem;
  width: 80px;
}

.logout-btn:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.2);
}

.logout-btn:focus {
  outline-color: var(--rosa);
}

.navbar-start {
  margin-right: 10px;
}


/* INFO STYLE */

.info {
  position: absolute;
  left: 15%;
  top: 45%;
}

.carousel-text{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 46%;
  height: 100%;
}

.infoText {
  color: #fff;
  font-size: 5rem;
  max-width: 400px;
  font-weight: 900;
  z-index: 100;
}

.infobtn {
  background-color: rgba(0, 0, 0, 0);
  font-size: 2rem;
  color: #fff;
  border: double 2px transparent;
  border-image-slice: 1;
  border-image-source: var(--gradiente);
  padding: 7px 10px;
  margin-top: 50px;
  font-weight: 900;
  z-index: 100;
  width: 200px;
  text-align: left;
}

.infobtn:focus {
  outline-color: var(--rosa);
}

.infobtn:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.fas {
  font-size: 1.6rem;
  margin-left: 5px;
}

/* PRINCIPAL STYLE */

.principal {
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.principal h1 {
  font-size: 3rem;
}

.principalText {
  display: flex;
  margin-top: 40px;
}

.rosa {
  margin-left: 10px;
  color: var(--rosa);
  font-weight: bold;
}

.principal-link {
  z-index: 100;
}

/* CARDS STYLE */


.cards {
  width: 60vw;
  height: 70vh;
  display: flex;
  flex-direction: column;
  margin: 50px 0;
}

.singleCard {
  display: flex;
  align-items: center;
  width: 100%;
  height: 33.33%;
  margin: 50px 0;
}

.cardInfo {
  background-color: #fff;
  width: 100%;
  height: 100%;
  padding: 30px 30px;
  box-shadow: 5px 5px 15px rgba(194, 194, 194, 0.9);
}

.flexend{
  align-items: flex-end;
  justify-content: flex-end;
  text-align: right;
  display: flex;
}

.cardInfo h2 {
  color: var(--vermelho);
  font-weight: 900;
}

.cardInfo p {
  font-size: 1.2rem;
  margin-top: 20px;
}

.mv1 h2::after{
  content: '';
	height: 1px;
	width: 30%;
	background: #000;
	display: block;
	margin: 10px 0;
}

.linha {
  height: 1px;
  width: 30%;
  background: #000;
}

.pontilhado {
  width: 50%;
  height: 100%;
  border: dotted 2px transparent;
  background-image: linear-gradient(white, white), radial-gradient(circle at top left, #D38312, #A83279);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.mv1{
  position: relative;
  top: 25px;
  left: 25px;
}

.mv2 {
  position: relative;
  top: 25px;
  right: 25px;
}

.as{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.principalBtn {
  margin-top: 300px;
  font-size: 2rem;
  background:  var(--gradiente);
  padding: 10px 100px;
  color: #fff;
  font-weight: 900;
  margin-bottom: 120px;
}

.principalBtn:focus {
  outline-color: var(--rosa);
}

.principalBtn:hover {
  transform: scale(1.1);
}

.icon1 {
  background: var(--gradiente);
  width: 40px;
  border-radius: 20px;
  z-index: 10;
  margin-top: 40px;
  padding: 5px
}

.icon2 {
  background: var(--gradiente);
  width: 40px;
  border-radius: 20px;
  z-index: 10;
  padding: 5px;
  margin-bottom: 40px;
}

/* FOOTER STYLE */

.footer {
  position: relative;
  left: 0;
  bottom: 0;
  background-color: var(--laranja);
  width: 100%;
  height: 5vh;
}
.apagar {
  text-align: center;
}


@media (min-width: 768px) and (max-width: 992px) {


  .infoText {
    font-size: 4rem;
    margin-left: 30px;
  }
  
  .infobtn {
    font-size: 1.6rem;
    padding: 7px 10px;
    margin-top: 20px;
  }

  .logout-btn{
    font-size: 1rem;
  }
  
  .cardInfo h2 {
    font-size: 1.5rem;
  }

  .pontilhado {
    width: 100%;
  }
}

@media (min-width: 545px) and (max-width: 768px) {
  .link {
    font-size: 1rem;
  }

  .logout-btn{
    font-size: 1rem;
  }

  .infoText {
    font-size: 3rem;
    margin-left: 30px;
  }
  
  .infobtn {
    font-size: 1.3rem;
    padding: 3px 5px;
    margin-top: 20px;
    width: 120px;
  }

  .fas {
    font-size: 1.1rem;
    margin-left: 2px;
  }

  .cardInfo h2 {
    font-size: 1.5rem;
  }

  .cardInfo p {
    font-size: 1rem;
    margin-top: 0;
  }

  .pontilhado {
    width: 100%;
  }

  .mv2 h2::after{
    content: '';
    height: 1px;
    width: 30%;
    background: #000;
    display: block;
    margin-top: 10px
  }
}

