@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
    --amarelo: #D38312;
    --rosa: #A83279;
    --vermelho: #B95350;
    --laranja: #C76D2E;
    --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    --azul: #050B42;
  }

.login {
    width: 100vw;
    height: 100vh;
    display: flex;
}

.form {
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form h1 {
    color: var(--azul);
    font-size: 5rem;
    font-weight: 900;
}

.input {
    width: 200px;
    height: 35px;
    border-radius: 5px;
    outline-color: var(--rosa);
    border:none;
    background-color: #F1F1F1;
    margin: 10px 0;
    padding: 5px 10px;
    font-size: 1.5rem;
}

.button {
    width: 160px;
    height: 30px;
    border-radius: 5px;
    background: var(--gradiente);
    color: #fff;
    font-size: 1.5rem;
    font-weight: 900;
    margin: 10px 0 20px 20px;
    border: none;
}

.button:hover {
    transform: scale(1.1);
}

.button:focus {
    outline-color: var(--rosa);
}

.span {
    margin-left: 15px;
    font-size: 1.5rem;
}

.entrar {
    font-size: 1.5rem;
    color: #000;
}

.entrar:hover {
    text-decoration: none;
    color: var(--rosa)
}

.signup {
    margin-left: 65px;
}

.image {
    width: 55%;
    background-image: url("/image/image-loginsignup");
    background-size: cover;
    background-repeat: no-repeat;
}


@media (min-width: 375px) and (max-width: 610px) {

    .form {
        width: 50%;
    }

    .form h1 {
        font-size: 3rem;
    }
    
    .input {
        width: 150px;
        margin-left: 15px;
    }
    
    .button {
        width: 120px;
        margin-left: 30px;
    }
    
    .span {
        margin-left: 15px;
        font-size: 1.2rem;
    }

    .entrar {
        font-size: 1.2rem;
    }

    .image {
        width: 50%;
        background-size: cover;
    }
}


@media (min-width: 320px) and (max-width: 375px) {

    .form {
        width: 50%;
    }

    .form h1 {
        font-size: 2rem;
    }
    
    .input {
        width: 120px;
        margin-left: 15px;
    }
    
    .button {
        width: 120px;
        margin-left: 15px;
    }
    
    .span {
        margin-left: 10px;
        font-size: 1.2rem;
    }

    .entrar {
        font-size: 1.2rem;
    }

    .image {
        width: 50%;
        background-size: cover;
    }
}



