@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
  --amarelo: #D38312;
  --rosa: #A83279;
  --vermelho: #B95350;
  --laranja: #C76D2E;
  --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%)
}

.cities {
    width: 100vw;
    height: 100vh;
}

.cities-header {
    background-color: var(--vermelho);
    width: 100vw;
    height: 40vh;
}

.cities-clouds {
    background-image: url("/image/cloud-nav.png");
    position: absolute;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100vw;
    height: 40vh;
    z-index: 2
}

.continentesBody {
    width: 100vw;
    height: 280vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.search-input {
    width: 500px;
    height: 40px;
    border-radius: 5px;
    border: none;
    outline: none;
    padding: 5px 10px;
    border-right: 0;
    font-size: 1.5rem;
}

.search{
    background-color: var(--vermelho);
    color: #fff;
    border-radius: 50%;
    font-size: 3rem;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchicon{
    color:#FFF; 
}

.searchicon:hover {
    border-bottom: 0 !important;
    color:#FFF; 
}

.searchSpace{
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 5px 15px rgba(194, 194, 194, 0.9);
    border-radius: 18px;
    width: 500px;
}

.citiesTitle {
    display: flex;
}

.citiesTitle h1{
    font-size: 2.5rem;
}

.citiesTitle h2{
    font-size: 2.5rem;
}

.vermelho {
    color: var(--vermelho);
    font-weight: 900;
    margin: 0 7px;
    font-size: 2.5rem;
}

.visitadas {
    margin-top: 100px;
}

.continentes {
    margin-top: 50px;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 30px;
}

.continent {
    margin: 30px 0;
}

.continent h3 {
    font-size: 1.6rem;
    background-color: var(--laranja);
    width: 180px;
    color: #fff;
    padding: 5px 10px;
    font-weight: 900;
    border-radius: 5px;
    margin-left: 50px;
}

.country{
    width: 100vw;
    display: flex;
    justify-content: space-around;
    margin: 30px 0;
}

.cityImg {
    width: 280px;
    border-radius: 5px;
    filter: grayscale(70%);
    min-width: 200px;
    min-height: 200px;
}

.cityImg:hover {
    filter: grayscale(0%);
}

.city {
    display: flex;
    flex-direction: column;
    width: 280px;
    height: 200px;
    border: dotted 3px var(--laranja);
    border-radius: 5px;
}


.cityInfo {
    position: relative;
    top: 25px;
    left: 25px;
}

.cityName {
    font-size: 3rem;
    color: #fff;
    font-weight: 900;
    position: absolute;
    right: 0;
    bottom: 10px;
}

.cityName:hover {
    text-decoration: none;
}

.cityLink{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    align-content: flex-end;
}

