@media (min-width: 1000px) and (max-width: 1200px) {
    .cityImg {
        width: 220px;
        min-height: 130px;
    }

    .city {
        width: 220px;
        height: 160px;
    }

    .cityName {
        font-size: 2.3rem;
    }

}

@media (min-width: 768px) and (max-width: 1000px) {
    .cityImg {
        width: 160px;
        min-width: 100px;
        min-height: 100px;
    }

    .city {
        width: 160px;
        height: 120px;
    }

    .cityName {
        font-size: 2rem;
    }

    .searchSpace{
        width: 300px;
    }

    .search{
        width: 60px;
    }
}

@media (min-width: 375px) and (max-width: 768px) {
    .cityImg {
        width: 100px;
        min-width: 80px;
        min-height: 80px;
    }

    .city {
        width: 100px;
        height: 80px;
        margin: 20px 0
    }

    .cityName {
        font-size: 1.5rem;
    }

    .country{
        flex-direction: column;
        align-items: center;
    }

    .continent h3 {
        text-align: center;
        width: 200px
    }

    .continentesBody {
        height: 500vh
    }

    .citiesTitle h1{
        font-size: 2rem;
    }
    
    .citiesTitle h2{
        font-size: 2rem;
    }

    .searchSpace{
        width: 300px;
    }

    .search{
        width: 40px;
    }
}


@media (min-width: 320px) and (max-width: 375px) {
    .cityImg {
        width: 100px;
        min-width: 80px;
        min-height: 80px;
    }

    .city {
        width: 100px;
        height: 80px;
        margin: 20px 0
    }

    .cityName {
        font-size: 1.5rem;
    }

    .country{
        flex-direction: column;
        align-items: center;
    }

    .continent h3 {
        text-align: center;
        width: 250px
    }

    .continentesBody {
        height: 500vh
    }

    .citiesTitle h1{
        font-size: 1.5rem;
    }
    
    .citiesTitle h2{
        font-size: 1.5rem;
    }

    .searchSpace{
        width: 300px;
    }

    .search{
        width: 40px;
    }
}