@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
  --amarelo: #D38312;
  --rosa: #A83279;
  --vermelho: #B95350;
  --laranja: #C76D2E;
  --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%)
}

.apagar {
    width: 100%;
    height: 100%;
}

.profile-body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-body h1 {
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 50px;
}

.roteiro{
    border: solid 2px;
    border-image-slice: 1;
    border-image-source: var(--gradiente);
    width: 30vw;
    z-index:1;
}

.roteiro-info {
    width: 30vw;
    display: flex;
    justify-content: space-around;
    margin: 20px 0;
}

.roteiro-details {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    width: 200px;
    margin-top: 30px;
}

.margin {
    display: flex;
    flex-direction: column;
}

.roteiro-btns {
    display: flex;
    justify-content: center;
}

.roteiro-btn {
    background-color: #fff;
    border: none;
    margin: 10px;
}

.roteiro-icon {
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.script-icon {
    position:absolute;
    top: 390px; 
    z-index:10;
    background: var(--gradiente);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color:#fff;
    padding: 10px;
    font-size: 2.5rem;
    text-align: center;
}

.linhaRoteiro {
    margin-left: 10px;
}

.logo {
    display: flex;

}

.placeCity {
    font-size: 2rem;
    font-weight: 900;
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
