@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap');

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
  --amarelo: #D38312;
  --rosa: #A83279;
  --vermelho: #B95350;
  --laranja: #C76D2E;
  --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%)
}

.singleCity {
    width: 100vw;
}

.singleCity-header {
    background-color: var(--vermelho);
    width: 100vw;
    height: 40vh;
}

.singleCity-clouds {
    background-image: url("/image/cloud-nav.png");
    position: absolute;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100vw;
    height: 40vh;
    z-index: 2
}

.categoriasBody {
    width: 100vw;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
}

.city-name {
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 900;
    font-size: 3rem;
    margin: 30px 0;
}

.primeiros {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    margin: 50px 0;
}

.cityPlace {
    width: 30vw;
    display: flex;
    justify-content: space-around;
    margin: 20px 0;
}

.place {
    display: flex;
    width: 25vw;
}

.cityIcon {
    width: 50px;
    height: 50px;
}

.placeName {
    font-size: 1.5rem;
}

.placeSub {
    font-size: 1.2rem;
}

.ultimos {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    margin: 30px 0;
}

.categoria{
    border: solid 2px;
    border-image-slice: 1;
    border-image-source: var(--gradiente);
    width: 30vw;
    position:relative; 
    z-index:1;
}

.categoriaName {
    text-align: center;
    font-weight: 900;
    margin: 30px 0;
}

.placeInfo {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.placeBtn {
    width: 20px;
    height: 20px;
    margin-top: 20px;
    border: none;
    background: var(--gradiente);
    color: #fff;
    font-size: 1.2rem;
    font-weight: 900;
    
}

.placeBtn:focus{
    outline-color: var(--rosa)
}

.placeBtn:hover {
    transform: scale(1.1);
}

.categoriaIcon{
    position:absolute;
    top:-30px; 
    z-index:10;
    background: var(--gradiente);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color:#fff;
    padding: 10px;
    font-size: 2.5rem;
    text-align: center;

}

.categoriaInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meuRoteiro-icon {
    width: 100px;
    margin: 10px 0 0 10px;
    position: absolute;
}

.citySpan {
    font-size: 2.5rem;
}


@media (min-width: 560px) and (max-width: 780px) {

    .bpzdHV {
        width: 300px;
    }

    .bpzdHV input {
        width: 300px;
    }

    .primeiros {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ultimos {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .categoria{
        width: 70vw;
        margin: 30px 0;
    }

    .cityPlace {
        width: 70vw;
    }
}

@media (min-width: 320px) and (max-width: 560px) {
    .bpzdHV {
        width: 300px;
    }

    .bpzdHV input {
        width: 300px;
    }

    .primeiros {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ultimos {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .categoria{
        width: 70vw;
        margin: 30px 0;
    }

    .cityPlace {
        width: 70vw;
    }

    .meuRoteiro-icon {
        width: 60px;
    }
}
