* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
    --amarelo: #D38312;
    --rosa: #A83279;
    --vermelho: #B95350;
    --laranja: #C76D2E;
    --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    --azul: #050B42;
  }

.login {
    width: 100vw;
    height: 100vh;
    display: flex;
}

.form {
    width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form h1 {
    color: #050B42;
    color: var(--azul);
    font-size: 5rem;
    font-weight: 900;
}

.input {
    width: 200px;
    height: 35px;
    border-radius: 5px;
    outline-color: #A83279;
