.cityIcon {
    width: 50px;
    height: 50px;
}

.placeName {
    font-size: 1.5rem;
}

.placeSub {
    font-size: 1.2rem;
}

.ultimos {
    width: 100vw;
    display: flex;
    justify-content: space-around;
    margin: 30px 0;
}

.categoria{
    border: solid 2px;
    border-image-slice: 1;
    border-image-source: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    border-image-source: var(--gradiente);
    width: 30vw;
    position:relative; 
    z-index:1;
}

.categoriaName {
    text-align: center;
    font-weight: 900;
    margin: 30px 0;
}

.placeInfo {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.placeBtn {
    width: 20px;
    height: 20px;
    margin-top: 20px;
    border: none;
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    color: #fff;
    font-size: 1.2rem;
    font-weight: 900;
    
}

.placeBtn:focus{
    outline-color: #A83279;
    outline-color: var(--rosa)
}

.placeBtn:hover {
    transform: scale(1.1);
}

.categoriaIcon{
    position:absolute;
    top:-30px; 
    z-index:10;
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color:#fff;
    padding: 10px;
    font-size: 2.5rem;
    text-align: center;

}

.categoriaInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meuRoteiro-icon {
    width: 100px;
    margin: 10px 0 0 10px;
    position: absolute;
}

.citySpan {
    font-size: 2.5rem;
}


@media (min-width: 560px) and (max-width: 780px) {

    .bpzdHV {
        width: 300px;
    }

    .bpzdHV input {
        width: 300px;
    }

    .primeiros {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ultimos {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .categoria{
        width: 70vw;
        margin: 30px 0;
    }

    .cityPlace {
        width: 70vw;
    }
}

@media (min-width: 320px) and (max-width: 560px) {
    .bpzdHV {
        width: 300px;
    }

    .bpzdHV input {
        width: 300px;
    }

    .primeiros {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .ultimos {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .categoria{
        width: 70vw;
        margin: 30px 0;
    }

    .cityPlace {
        width: 70vw;
    }

    .meuRoteiro-icon {
        width: 60px;
    }
}

* {
  margin: 0;
  padding: 0;
  font-size: 10px;
  font-family: 'Lato', sans-serif;
}

:root {
  --amarelo: #D38312;
  --rosa: #A83279;
  --vermelho: #B95350;
  --laranja: #C76D2E;
  --gradiente: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%)
}

.apagar {
    width: 100%;
    height: 100%;
}

.profile-body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-body h1 {
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: 900;
    font-size: 4rem;
    margin-bottom: 50px;
}

.roteiro{
    border: solid 2px;
    border-image-slice: 1;
    border-image-source: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    border-image-source: var(--gradiente);
    width: 30vw;
    z-index:1;
}

.roteiro-info {
    width: 30vw;
    display: flex;
    justify-content: space-around;
    margin: 20px 0;
}

.roteiro-details {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    width: 200px;
    margin-top: 30px;
}

.margin {
    display: flex;
    flex-direction: column;
}

.roteiro-btns {
    display: flex;
    justify-content: center;
}

.roteiro-btn {
    background-color: #fff;
    border: none;
    margin: 10px;
}

.roteiro-icon {
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.script-icon {
    position:absolute;
    top: 390px; 
    z-index:10;
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color:#fff;
    padding: 10px;
    font-size: 2.5rem;
    text-align: center;
}

.linhaRoteiro {
    margin-left: 10px;
}

.logo {
    display: flex;

}

.placeCity {
    font-size: 2rem;
    font-weight: 900;
    background: linear-gradient(90deg, rgba(211,131,18,1) 0%, rgba(168,50,121,1) 100%);
    background: var(--gradiente);
    background-clip: text;
	-webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

